.brandname{
  margin-left:10px !important;
  font-family:'julius';
  color:#000!important;
 
}


*{
  max-width: 100% ;
}



.header-wrapper{
  position:relative;
  background: url(./images/headerpic.jpg) no-repeat;
  background-size: cover;
  background-position:center;
  height:85vh;
  width: 100%;
  filter: brightness(110%);
 
}

.main-info{
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
  
  justify-content: center;
  align-items: center;
  text-align: center;
 
}

.main-info h2{
  font-family: poiret;
  /* font-weight: bold; */
  font-size: 55px;
  color:#000;
  text-shadow:
   -1px -1px 0 #fff,  
    1px -1px 0 #fff,
    -1px 1px 0 #fff,
     1px 1px 0 #fff;
  
}


.nav-link,.nav-links{
  
  font-family: julius;
  color:#000 !important;
  font-size: 20px !important;
  
}

.navbar-toggler{
  border: 2px solid #000 !important;
  color: #000;
  z-index: 1;
}

.typed-text{
  font-family: special;
  
  font-weight: bold;
  color:var(--primary);
  color:#fff;
  letter-spacing: 5px;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
}


/****************HEADER BUTTON******************/
.buttonBox{
  font-family: poiret;
  z-index: 2;
  border: 2px solid #000 ;
  padding: 10px 15px;
  font-size: 30px;
  text-decoration: none;
  color: #000;
  text-shadow:
   -0.6px -0.6px 0 #fff,  
   0.6px -0.6px 0 #fff,
    -0.6px 0.6px 0 #fff,
    0.6px 0.6px 0 #fff !important;

  padding:3px 5px;
  border-radius:5px;
}


.contact>a{
  transition:all .5s ease-in-out;
}

.contact:hover a{
  background:#121212;
  color:white;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000 !important;
}

.view>a{
  transition:all .3s ease-in-out;
}

.view:hover a{
  background:#000;
  color:skyblue;
  text-shadow:
   -1px -1px 0 #000,  
   1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000 !important;
}

.navbar-toggler-icon{
  color:#fff;
}


/***************CARDS******************/
.category{
  margin-top:40px !important;
  margin:25px;
  padding: 5px;
  box-shadow: 3px 3px gray;
}

.cardtitle{
  font-family: Georgia, 'Times New Roman', Times, serif;
}

