/************************ SMALL ***********************/
@media(max-width:640px ){
  .main-info{
    top:290px;
  }
  .logo{
    width: 90% !important;
  }
  .brandname{
    font-size: 25px !important;
  }
  .typed-text{
    font-size:20px;
  }
  #shopname{
    font-size:25px;
  }
  .nav-links{
    font-size: 18px;
    text-decoration: none;
  }
  .nav-link{
    font-size: 18px;
    
  }
  .category{
    margin-left: 20px;
  }
  .cardbody{
    margin: 30px;
  }
  .contact{
    font-size: 20px;
  }

   #caro{
    margin-top: 20px !important;
    margin-bottom: 30px;
   }

   .contact-form{
     font-family: Georgia, 'Times New Roman', Times, serif;
     font-size: 15px;
   
     padding: 10px;
   }
   .contact-form input{
    margin:5px;
   }
   .contact-form .buttonBox{
     font-size: 20px;
   }
   
   .contact-form textarea{
     width: 90%;
   }
   
}


 








/************************ Large ***********************/
@media(min-width:1368px){
  .header-wrapper{
    position:relative;
    background: url(./images/headerpic.jpg) no-repeat;
    background-size: cover;
    background-position:center;
    height:100vh;
    filter: brightness(110%);
  }
  .category{
    margin-left:60px;
  }
  .logo{
    width:80%;
    position: relative;
    
    filter: brightness(110%);
    object-fit: scale-down !important;
  }
  .typed-text{
    font-size: 40px;
  }
  .cardbody{
  
    margin-top: 70px;
  }
  .brandname{
    margin-left: 60px !important;
    font-size: 40px !important;
  }
  .nav-link{
    font-size: 35px;
    margin-left: 180px;
  }

  .sidenav{
    margin-left: 10px;
    margin-top: 70px !important;
    
    border-right: 1px solid #000;
 
   
  }
  .nav-links{
  
    text-decoration: none;
  }
  
  #caro{
    margin-top:80px !important;
    margin-left: 40px;
    
  }

  .foot{
    margin-top: 20px;
  }


}


/***************Footer******************/
.foot{
  background-color: #f7f7f7;
  padding: 10px;
}
.social-container {
  background: #eee;
  padding: 25px 50px;
}

a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}

a.youtube {
  color: #eb3223;
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: black;
}